.map_border {
  fill: #aadaff;
  stroke: darkblue;
  stroke-width: .5;
  opacity: .5; }

.graticule {
  fill: none;
  stroke: blue;
  stroke-width: .2;
  opacity: .4; }

.tropics {
  fill: none;
  stroke: red;
  opacity: .5;
  stroke-width: .2; }

.tropic0 {
  fill: none;
  stroke: red;
  opacity: .5;
  stroke-width: .5; }

.land {
  fill: Tan;
  fill-opacity: 0.8;
  stroke: black; }

.countries {
  fill: #ffedcd;
  fill-opacity: .5;
  stroke: black; }

.countries path:hover {
  fill: pink;
  fill-opacity: .5;
  stroke: red;
  stroke-width: .1; }

.glow_ball1 {
  position: absolute;
  left: 210px;
  top: 282px;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  box-shadow: inset 0 0 50px #fff, inset 20px 0 80px #f0f, inset -20px 0 80px #0ff, inset 20px 0 400px #f0f, inset -20px 0 400px #0ff, 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
  /* outer right cyan */ }

.glow_ball0 {
  position: absolute;
  left: 210px;
  top: 282px;
  width: 400px;
  height: 400px;
  background-color: #aadaff;
  border-radius: 50%;
  box-shadow: inset -25px -25px 40px rgba(0, 0, 0, 0.5);
  background-image: linear-gradient(-45deg, rgba(255, 255, 220, 0.2) 0%, transparent 100%);
  z-index: -1; }

.btnDemo {
  background-color: white;
  border: 2px solid #4caf50;
  color: black;
  padding: 4px 4px;
  font: x-small "Courier New", sans-serif;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px; }

.report {
  font: small "Courier New", sans-serif;
  text-align: left;
  text-indent: 10px;
  line-height: 10px; }

